<template >
  <div class="grid" >
    <h2 class="grid__title">{{langWord.infobar[lang]}}</h2>
    <button class="grid__btn" v-if="grid" @click="$emit('changeGrid',!grid)">
      <img src="../assets/img/list.svg" alt="" />
      {{langWord.list[lang]}}
    </button>
    <button class="grid__btn" v-else @click="$emit('changeGrid',!grid)">
      <img src="../assets/img/grid.svg" alt="" />
      {{langWord.grid[lang]}}
    </button>
  </div>
</template>
<script>
export default {
    props:{
        grid:Boolean,
        lang:String,
        langWord: Object
    }
};
</script>
