<template>
  <div class="box" :class="{ grid__box: grid, list__box: !grid }">
    <div class="card" v-for="(note, i) in notes" :key="i">
      <h2 class="card__title">{{ note.title }}</h2>
      <p class="card__time">{{ note.time }}</p>
      <p class="card__text">
        {{ note.content }}
      </p>
      <div class="card__btn" @click="$emit('changeNote',note.id)">
        <button class="card__btn-edit">
          <img src="../assets/img/change.svg" alt="" />
          {{langWord.editbtn[lang]}}
        </button>
        <button class="card__btn-delete" @click="delCard(note.id)">
          <img src="../assets/img/delete.svg" alt="" />
          {{langWord.deledit[lang]}}
        </button>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  props: {
    grid: Boolean,
    notes: Array,
    lang:String,
    langWord: Object
  },
  methods:{
    delCard(id){
      this.notes.forEach((note,i)=>{
        if (note.id==id) {
          this.notes.splice(i,1)
        }
      })
    }
  }
};
</script>
<style lang="">
</style>