<template>
  <Nav
    :lang="lang"
    @change="lang = $event"
    :search="search"
    @changeNav="search = $event"
    @back="search = $event"
    @searchVal="searchInput = $event"
    :langWord="langWord"
  />
  <div class="container">
    <Grid
      :grid="grid"
      @changeGrid="grid = $event"
      :lang="lang"
      :langWord="langWord"
    />
    <Cards
      :grid="grid"
      :notes="notesFilter"
      @changeNote="changeNote"
      :lang="lang"
      :langWord="langWord"
    />
  </div>
  <Transition name="modal">
    <Modal
      v-if="modal"
      :modal="modal"
      @cancel="(modal = $event), (addOrChange = true)"
      @addNote="addCard"
      :addOrChange="addOrChange"
      :saveChange="saveChange"
      @saveNote="saveNote"
      :lang="lang"
      :langWord="langWord"
    />
  </Transition>

  <AddBtn @changeModal="modal = $event" />
</template>
<script>
import Nav from "./components/Nav.vue";
import Grid from "./components/Grid.vue";
import Cards from "./components/Cards.vue";
import AddBtn from "./components/AddBtn.vue";
import Modal from "./components/Modal.vue";
import lang from "./lang";
export default {
  components: {
    Nav,
    Grid,
    Cards,
    AddBtn,
    Modal,
  },
  data() {
    return {
      lang: "uz",
      search: false,
      grid: true,
      modal: false,
      notes: [],
      id: 1,
      addOrChange: false,
      saveChange: {},
      searchInput: "",
      langWord: {},
    };
  },
  methods: {
    addCard(cardObj) {
      (cardObj.id = this.id++), this.notes.push(cardObj);
      this.modal = false;
    },
    changeNote(cardId) {
      this.notes.forEach((card) => {
        if (card.id == cardId) {
          this.saveChange = {
            id: card.id,
            title: card.title,
            content: card.content,
          };
          this.modal = true;
          this.addOrChange = true;
        }
      });
    },
    saveNote(cardObj) {
      this.notes.forEach((card, i) => {
        if (card.id == cardObj.id) {
          card.title = cardObj.title;
          card.content = cardObj.content;
          card.time = cardObj.time;
          this.modal = false;
          this.addOrChange = false;
        }
      });
    },
  },
  computed: {
    notesFilter() {
      if (!this.search) return this.notes;
      let search = this.searchInput;
      search = search.trim().toLowerCase();
      let array = this.notes.filter((note) => {
        if (note.title.toLowerCase().indexOf(search) != -1) return note;
      });
      return array;
    },
  },
  created() {
    this.langWord = lang;
    let localId = localStorage.getItem("id")
    let localNotes = localStorage.getItem("notes")
    if(localId) {
      this.id = JSON.parse(localId)
    }
    if (localNotes) {
      this.notes = JSON.parse(localNotes)
    }
  },
  watch:{
    id(){
      localStorage.setItem('id', JSON.stringify(this.id));
    },
    notes: {
      handler(array) {
        localStorage.setItem("notes", JSON.stringify(array));
      },
      deep: true,
    }
  }
};
</script>
<style lang="scss">
.modal-enter-active,
.modal-leave-active {
  transition: all 0.5s ease;
}
.modal-enter-from,
.modal-leave-to {
  opacity: 0;
  transform: scale(1.2);
}
</style>
