<template >
    <div class="modal" >
      <div class="modal__card">
        <h2 class="modal__card-title" v-if="!addOrChange">{{langWord.titlewindow[lang]}}</h2>
        <h2 class="modal__card-title" v-else>{{langWord.titlewindowedit[lang]}}</h2>titlewindowedit
        <label>
          Title
          <input type="text" placeholder="Title" v-model="title" />
        </label>
        <label>
          Content
          <input type="text" placeholder="Content" v-model="content" />
        </label>
        <div class="modal__card-btns">
          <button class="card__btn-delete" @click="cancel">{{langWord.closebtn[lang]}}</button>

          <button class="card__btn-edit" v-if="!addOrChange" @click="addNote">
            {{langWord.addbtn[lang]}}
          </button>
          <button class="card__btn-edit" v-else @click="saveNote">{{langWord.editwindowbtn[lang]}}</button>
        </div>
      </div>
    </div>
</template>
<script>
export default {
  props: {
    addOrChange: Boolean,
    saveChange: Object,
    lang:String,
    langWord: Object
  },
  data() {
    return {
      title: "",
      content: "",
    };
  },
  methods: {
    cancel() {
      this.title = "";
      this.content = "";
      this.$emit("cancel", false);
    },
    saveNote(){
      if (this.title != "" && this.content !="") {
        this.$emit("saveNote",{
          id:this.saveChange.id,
          title:this.title,
          content:this.content,
          time: new Date().toLocaleDateString()
        })
        this.title = ""
        this.content = ""
      }
    },
    addNote() {
      if (this.title != "" && this.content != "") {
        this.$emit("addNote", {
          title: this.title,
          content: this.content,
          time: new Date().toLocaleDateString(),
        });
        (this.content = ""), (this.title = "");
      }
    },
  },
  mounted() {
    if (this.addOrChange) {
     this.title = this.saveChange.title
     this.content = this.saveChange.content
    }
  },
};
</script>
